<template>
    <div>
        <div class="container_fluid">
            网络管理系统
        </div>
        <div class="header_user">
            <span @click="buttoncli" :class="fullscreen==false?'screenfull_icon iconfont icon-expand':'screenfull_icon iconfont icon-tuichuquanping-copy'"></span>
            <img src="/static/img/icon-user.png" alt="" class="user_img">
            <span>{{userName}}</span>
            <span>|</span>
            <span id="logout" @click="logout">退出</span>
        </div>
    </div>
</template>

<script>
import screenfull from "screenfull";
export default {
    data() {
        return {
            fullscreen: false,
            userName: ""
        };
    },
    methods: {
        buttoncli() {
            if (!screenfull.enabled) {
                // 如果不允许进入全屏，发出不允许提示
                this.$message({
                    message: "该浏览器不支持全屏",
                    type: "warning"
                });
                return false;
            }
            let element = document.documentElement;
            if (this.fullscreen) {
                if (document.exitFullscreen) {
                    document.exitFullscreen();
                } else if (document.webkitCancelFullScreen) {
                    document.webkitCancelFullScreen();
                } else if (document.mozCancelFullScreen) {
                    document.mozCancelFullScreen();
                } else if (document.msExitFullscreen) {
                    document.msExitFullscreen();
                }
            } else {
                if (element.requestFullscreen) {
                    element.requestFullscreen();
                } else if (element.webkitRequestFullScreen) {
                    element.webkitRequestFullScreen();
                } else if (element.mozRequestFullScreen) {
                    element.mozRequestFullScreen();
                } else if (element.msRequestFullscreen) {
                    // IE11
                    element.msRequestFullscreen();
                }
            }
            this.fullscreen = !this.fullscreen;
        },
        logout() {
            this.$confirm("是否确定退出登录?", "提示", {
                cancelButtonClass: "btn-custom-cancel",
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    var timestamp = new Date().valueOf();
                    let username = sessionStorage.getItem("access_username");
                    let sessionId = sessionStorage.getItem("access_token");
                    console.log(username);
                    console.log(sessionId);
                    let aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "Authentication",
                                CommandData: {
                                    CommandName: "logout",
                                    UserName: username
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                    let that = this;
                    this.$axios
                        .post("/cmd", aoData)
                        .then(function(response) {
                            if (response.data.status == "success") {
                                sessionStorage.clear();
                                that.$router.replace("/pages/login");
                                if (document.exitFullscreen) {
                                    document.exitFullscreen();
                                } else if (document.webkitCancelFullScreen) {
                                    document.webkitCancelFullScreen();
                                } else if (document.mozCancelFullScreen) {
                                    document.mozCancelFullScreen();
                                } else if (document.msExitFullscreen) {
                                    document.msExitFullscreen();
                                }
                            } else if (response.data.status == "fail") {
                                alert(response.data.error.errorstring.cn);
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                })
                .catch(() => {});
        }
    },
    mounted() {
        this.$nextTick(function() {
            this.userName = sessionStorage.getItem("access_username");
        });
    }
};
</script>
<style>
.btn-custom-cancel {
    float: right;
    margin-left: 10px;
}
</style>

<style scoped lang="scss">
@import "../style/common";
@import "../style/fontIcon/iconfont.css";
.header_user {
    display: flex;
    flex-direction: row;
    color: white;
    /*width: 200px;*/
    height: 40px;
    align-items: center;
}
.user_img {
    width: 17px;
    height: 17px;
}
.header_user span {
    margin-right: 15px;
    line-height: 40px;
    font-size: 14px;
}
.header_user img {
    margin-right: 15px;
}
#logout {
    cursor: pointer;
}
.screenfull_icon {
    cursor: pointer;
}
</style>



