<template>
    <div>
        <Header class="header"></Header>
        <div class="main_section" >
            <Navleft class="aside_nav" @nav_switch="changeNavInfo" :style="aside_nav[selectIndex]"></Navleft>
            <div class="mainFrame" :style="main_section[selectIndex]">
                <router-view/>
            </div>
        </div>
    </div>
</template>


<script>
import Header from "../components/header";
import Navleft from "../components/navleft";

export default {
    name: "indexHtml",

    data() {
        return {
            aside_nav: ["width:60px", "width:180px"],
            main_section: ["margin-left:60px", "margin-left:180px"],
            selectIndex: 1
        };
    },
    components: {
        Header,
        Navleft
    },
    methods: {
        changeNavInfo: function(data) {
            if (data == "close_nav") {
                this.selectIndex = 0;
            } else {
                this.selectIndex = 1;
            }
        }
    }
};
</script>

<style lang="scss"scoped>
@import "../style/common";

#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
}

.header {
    height: 40px;
    width: 100%;
    background-color: #2c2c2c;
    /*box-shadow: 0 3px 10px 3px rgba(77, 77, 77, 0.45);*/
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    align-items: center;
    justify-content: space-between;
}

.main_section {
    position: fixed;
    left: 0;
    width: 100%;
    bottom: 0;
    top: 40px;
    box-sizing: border-box;
}

.aside_nav {
    width: 180px;
    height: 100%;
    background-color: #4d4d4d;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    transition: width 0.5s;
    -moz-transition: width 0.5s;
    -webkit-transition: width 0.5s;
    -o-transition: width 0.5s;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    list-style: none;
    overflow: hidden;
}
.mainFrame {
    transition: margin 0.5s;
    -moz-transition: margin 0.5s;
    -webkit-transition: margin 0.5s;
    -o-transition: margin 0.5s;
   -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
}
</style>



